import "./style.scss";

import { useNavigate, useSearchParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Table from "../../../components/table";

// components
import Button from "../../../components/button";
import Breadcrumb from "../../../components/breadcrumb";
import { useBOM } from "../../../store/materialPlanner";
import { useOrder, useScanning } from "../../../store/materialPlanner";
import FailureReasonSuccessModal from "./FailureReasonSuccessModal";

function AddfailureReasonPage() {
  const {
    postFailureReason,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();

  const { getFailureReasons } = useScanning();

  const [failureReasonsData, setFailureReasonsData] = useState(null);
  const [isfailureReasonInputFormOpen, setIsfailureReasonInputFormOpen] =
    useState(false);
  const [failureReasonText, setFailureReasonText] = useState(null);
  const [isResolutionDateRequired, setIsResolutionDateRequired] =
    useState(null);
  const [isfailureReasonSuccessModalOpen, setIsFailureReasonSuccessModalOpen] =
    useState(false);

  const handleSubmit = () => {
    postFailureReason(failureReasonText, isResolutionDateRequired)
      .then((data) => {
        console.log("postFailureReason data", data);
        if (data && data.message === "OK") {
          setIsFailureReasonSuccessModalOpen(true);
          setIsfailureReasonInputFormOpen(false);
        }
      })
      .catch((error) => {
        console.error("postFailureReason error", error);
      });
  };

  useEffect(() => {
    getFailureReasons().then((data) => {
      console.log("getFailureReasons data", data);
      setFailureReasonsData(data);
    });
  }, [isfailureReasonSuccessModalOpen]);

  return (
    <div className="mx-3 my-2 mp-instruction-layout">
      {/* Header   */}
      <div className="mp-instruction-header">
        <p className="instruction-text">
          <strong>Failure reasons:</strong>
        </p>
      </div>
      {/* Body   */}
      <div className="material-planner-layout-body">
        <div className="failure-reasons-list-container">
          {failureReasonsData &&
            Object.entries(failureReasonsData).map(([key, value]) => (
              <div key={key}>
                <li>{value.name}</li>
              </div>
            ))}
        </div>
        {!isfailureReasonInputFormOpen && (
          <Button
            onClick={() => {
              setIsfailureReasonInputFormOpen(true);
            }}
            color="blue"
            customStyle={{
              borderRadius: "0.5vw",
              border: "1px solid grey",
              padding: "0.7vw",
              marginBottom: "1rem",
              marginTop: "1rem",
              fontSize: "1rem",
            }}
          >
            Add a new Failure Reason
          </Button>
        )}
        {isfailureReasonInputFormOpen && (
          <form action="" method="post" className="failure-reason-form">
            <div>
              <input
                type="text"
                name="failureReason"
                placeholder={`Write failure reason here`}
                value={failureReasonText}
                onChange={(e) => {
                  console.log(`Updated value: ${e.target.value}`);
                  setFailureReasonText(e.target.value);
                }}
                style={{
                  borderRadius: "0.5vw",
                  border: "1px solid grey",
                  padding: "0.75vw",
                  marginBottom: "1rem",
                  fontSize: "1rem",
                  width: "25vw",
                }} // Adjust styling as needed
              />
            </div>
            <div style={{ fontSize: "1rem" }}>Resolution Date required?</div>
            <div className="checkbox-items-container">
              <div>
                <label>
                  Yes
                  <input
                    type="checkbox"
                    name="isResolutionDateRequiredYes"
                    checked={isResolutionDateRequired === true}
                    onChange={(e) => {
                      console.log(`Yes checkbox updated: ${e.target.checked}`);
                      setIsResolutionDateRequired(
                        e.target.checked
                          ? true
                          : isResolutionDateRequired === false
                          ? null
                          : false
                      );
                    }}
                  />
                </label>
              </div>
              <div>
                <label>
                  No
                  <input
                    type="checkbox"
                    name="isResolutionDateRequiredNo"
                    checked={isResolutionDateRequired === false}
                    onChange={(e) => {
                      console.log(`No checkbox updated: ${e.target.checked}`);
                      setIsResolutionDateRequired(
                        e.target.checked
                          ? false
                          : isResolutionDateRequired === true
                          ? null
                          : true
                      );
                    }}
                  />
                </label>
              </div>
            </div>
            <Button
              onClick={handleSubmit}
              color="blue"
              customStyle={{
                borderRadius: "0.5vw",
                border: "1px solid grey",
                padding: "0.7vw",
                marginBottom: "1rem",
                fontSize: "1rem",
              }}
              disabled={!failureReasonText || isResolutionDateRequired === null}
            >
              Submit
            </Button>
          </form>
        )}
      </div>
      {/* Failure Reason submit success Modal */}
      {isfailureReasonSuccessModalOpen && (
        <FailureReasonSuccessModal
          open={isfailureReasonSuccessModalOpen}
          onClose={() => {
            setIsFailureReasonSuccessModalOpen(false);
            setFailureReasonText("");
          }}
          failureReasonText={failureReasonText}
          isResolutionDateRequired={isResolutionDateRequired}
          nextStepFn={() => {
            setIsFailureReasonSuccessModalOpen(false);
          }}
        />
      )}
    </div>
  );
}

export default AddfailureReasonPage;
